<template>
  <div>
    <Test v-if="my_items" id="popup" :distanza="my_distance" :title="my_title" :items="my_items" ref="htmlPopup" :open="my_open_quest" :validate="my_validate_quest" :location="location_permission" />
    <div class="map-container" ref="myMapp"></div>   
    <NoLocation ref="no_location_alert"/>
  </div>
</template>

<script>
import maplibre from 'maplibre-gl';
import Test from './Test.vue';
import NoLocation from './popups/NoLocation.vue';


export default {
  name: "MyTiles",
  components:{
    Test,
    NoLocation
  },
  data(){
    return{public_path: process.env.BASE_URL, my_distance:null, location_permission:null, map:null, completed_w:null, completed_n:null, my_items:null, my_title:"", page:this, first_time:0, my_open_quest:null, my_validate_quest:null}
  },
  mounted: async function(){
    this.askGeoLocation();
    const ref=this
    const mapStyle = "https://api.maptiler.com/maps/topo/style.json?key=S1aB9ElJLePXra57G3cm"
    console.log(mapStyle)
    const initialState = {
      lng: 11.116667,
      lat: 46.066666,
      zoom: 15
    };

    this.map = new maplibre.Map({
      container: this.$refs.myMapp,
      style: `${mapStyle}`,
      center: [initialState.lng, initialState.lat],
      zoom: initialState.zoom,
      maxZoom: 17,
      //interactive: false
    });
    this.map.setMaxZoom(17);
    const way_url = this.$api_url + "/tippecanoe/allWaysSecondTry/{z}/{x}/{y}.pbf";
    const node_url = this.$api_url + "/tippecanoe/allNodes/{z}/{x}/{y}.pbf"
    const center_json_url = this.$api_url+"/tippecanoe/centerCombined.geojson"
    //const node_url = this.public_path + "tippecanoe/allNodes/{z}/{x}/{y}.pbf" //SE INSERISCO I DATI NEL SITO STESSO
    //const way_url = this.public_path+"tippecanoe/allWaysSecondTry/{z}/{x}/{y}.pbf" //SE INSERISCO I DATI NEL SITO STESSO
    //const center_json_url = this.public_path+"tippecanoe/centerCombined.geojson"  //SE INSERISCO I DATI NEL SITO STESSO
    console.log(node_url);
    console.log(way_url);
    console.log(center_json_url);
    //PRIMA DI TUTTO FACCIO CHIAMATA CHE MI DICE QUALI VIE MOSTRARE E QUALI NO
    var my_completed_ways_dt = await this.getAllCompletedWaysAndNodes(function(){
      console.log("finished getting all ways....")
    },"way")
    console.log("AM I GOING AFTER THE FUNCTION OF ALL WAYS?" + my_completed_ways_dt);
    var my_completed_ways = []
    for(var i in my_completed_ways_dt){
      console.log(my_completed_ways_dt[i].id)
      my_completed_ways.push(my_completed_ways_dt[i].id)
    }
    if(my_completed_ways.length == 0){
      my_completed_ways.push(0);
    }
    this.completed_w = my_completed_ways;
    //console.log(my_completed_ways)
    var my_completed_nodes_dt = await this.getAllCompletedWaysAndNodes(function(){
      console.log("finished getting all nodes....")
    },"node")
    console.log("AM I GOING AFTER THE FUNCTION OF ALL NoODES?" + my_completed_nodes_dt);
    //console.log(my_completed_nodes_dt)
    var my_completed_nodes = []
    for(var j in my_completed_nodes_dt){{
      //console.log(my_completed_nodes_dt[j].id)
      my_completed_nodes.push(my_completed_nodes_dt[j].id)
    }}
    if(my_completed_nodes.length == 0){
      my_completed_nodes.push(0)
    }
    this.completed_n = my_completed_nodes;
    this.map.addControl(new maplibre.NavigationControl());
    var geolocate = new maplibre.GeolocateControl({
        fitBoundsOptions: {
            zoom: 16,
        },
        positionOptions: {
          enableHighAccuracy: true
        },
        showUserHeading: true, //freccia che mostra dove guardo
        trackUserLocation: true,
        showAccuracyCircle: false, //cerchio che mostra accuracia con cui va la geolocation
    })
    //////////////////////////////// DISABLE ZOOMING ECC...
    //ref.map.scrollZoom.disable();
    //ref.map.doubleClickZoom.disable();
    //add geolocate control
    this.map.addControl(
      geolocate
    );
    this.map.maxZoom=17;
    this.map.on('load',function(){
      console.log("START OF MAP LOAD");
      geolocate.trigger();
      geolocate._accuracyCircleMarker._color= "#DC143C";
      ref.map.resize();
      ref.map.addSource('my_ways',{
          type:'vector',
          tiles:[way_url], //["http://localhost:8081/tippecanoe_funzionante/allWays/{z}/{x}/{y}.pbf"], 
          'minzoom': 14,
          'maxzoom': 21,
      });
      ref.map.addLayer({
        'id': "my_way_data",
        'type': 'line',
        'source': 'my_ways',
        'source-layer': 'combined',
        'maxzoom':21,
        'minzoom' : 10,
        'layout':{
                'line-join': 'round',
                'line-cap': 'round',
                //"visibility":'none',
              },
              'paint': {
                //'line-color': ["get","color"],
                "line-color":[
                  "match",
                  ["get","id"],
                  //[935556427,303166647], // una lista penso
                  my_completed_ways,
                  "transparent", //se metto transparent scompare ma  cliccabile
                  "#6667AB"//["get","color"], //"black",
                ],
                //'line-blur':5,
                //"line-opacity":0.0,
                'line-width': 9,
              },
      })
      //se vuoi togliere tutte le interazioni metti su variabile mappa interactive:false
      ref.map.on('click', 'my_way_data',function(e){
        //PRIMA DI TUTTO DEVO VERIFICARE SE LA VIA  DA VISUALIZZARE O MENO! QUINDI VEDO SE  NELLE VIE COMPLETE O NO.
        if(!ref.completed_w.includes(e.features[0].properties.id)){ 
          ref.hideAllExceptOneWay(ref.map,e.features[0].properties.id, "my_way_data");
          ref.hideAllExceptOneCentered(ref.map,e.features[0].properties.id, "my_center_data");
          console.log("THIS IS THE POINT CLICKED")
          console.log(e.lngLat);
          ref.map.flyTo({
            center: e.lngLat,
            zoom:17
          })
          //ref.$refs.question_popup.showDialog=true;
          var AllTags = JSON.parse(e.features[0].properties.tags);
          var nameTag = AllTags.name;
          var highwayType = AllTags.highway;
          console.log(AllTags);
          console.log(nameTag);
          if(nameTag==undefined||nameTag=="undefined"){
            if(highwayType==undefined||highwayType=="undefined"){
              nameTag = e.features[0].properties.type;
            }else{
              nameTag = "strada: " + highwayType
            }
          }
          ref.my_title = nameTag;
          ////
          console.log(ref.map.getLayer("my_way_data"));
          var features = ref.map.queryRenderedFeatures(e.point);
          console.log(features)
          console.log(e.features[0])
          //console.log(e)
          //qua dovrei fare na chiamata e aspettare la risposta per poi caricare le doamnde corrispondenti al dato elemento.
          ref.getQuestion(e.features[0].properties.id,"WAY").then(items =>{
            console.log("THESE ARE THE POSSIBLE ANSWERS " + items[0].ANSWERS);
            const open_list = []
            const validate_list = []
            if(items.length!=0){
              for(var i in items){
                if(items[i].VALIDATING=="no"){
                  open_list.push(items[i])
                }else{
                  validate_list.push(items[i])
                }
              }
              console.log(validate_list);
              ref.my_items = items;
              ref.my_open_quest = open_list;
              ref.my_validate_quest = validate_list;
              ref.my_title = nameTag;
              console.log(ref.my_title);
              console.log("USER LOCATION:" + geolocate._userLocationDotMarker._lngLat);
              console.log("CLICK LOCATION:" + e.lngLat);
              console.log("LOCATION PERMISSION: " + ref.location_permission);
              var distance;
              distance = ref.checkDistance(geolocate, e);
              //console.log("THIS IS DISTANCE: " + distance);
              ref.my_distance = distance;
              ref.$nextTick(()=>{
                var my_popup = new maplibre.Popup().
                setLngLat(e.lngLat).
                setDOMContent(ref.$refs.htmlPopup.$el).
                //setHTML(ref.$refs.htmlPopup.$el.outerHTML).
                addTo(ref.map);
                //console.log(my_popup);
                //console.log(my_popup._container.style)
                my_popup._container.style.backgroundColor = "rgba(255,255,255,0.8)";
                my_popup._content.style.backgroundColor= "rgba(255,255,255,0.7)";
              })
            }        
          })
        }

      })
      ref.map.on('mouseenter', 'my_way_data', function () {
        ref.map.getCanvas().style.cursor = 'pointer';
      });

      ref.map.addSource('my_nodes',{
          type:'vector',
          tiles: [node_url],//["http://localhost:8081/tippecanoe_funzionante/allNodes/{z}/{x}/{y}.pbf"], 
          'minzoom': 14,
          'maxzoom' : 22
      });
      console.log("YOOOOOOOOOOOOOOOOOOOOOOOOOOOOOOOO")
      console.log(my_completed_nodes);  
      ref.map.addLayer({
        'id': "my_node_data",
        //'type': 'symbol',
        'type':"circle",
        'source': 'my_nodes',
        'source-layer': 'combinedNodes',
        'maxzoom':22,
        'minzoom' : 14,
        'paint':{
          'circle-radius': 15,
          'circle-stroke-width': 3,
          'circle-color': ["match", ["get","id"],my_completed_nodes,"transparent",'#E4455E'],
          'circle-stroke-color': ["match", ["get","id"],my_completed_nodes,"transparent",'#E4455E']
        }
      });
      //  /*'layout':{
      //          'icon-image': 'my_icon',
      //          'icon-size' :0.08,
      //        }
      //})
      //ref.map.loadImage(require('@/assets/images/placeholder.png'),function(error,image){
      //  if(error) throw error;
      //  ref.map.addImage('my_icon', image);
      //});
      ref.map.on('click', 'my_node_data',function(e){
        ref.hideAll(ref.map);
        ref.hideAllExceptOneNode(ref.map,e.features[0].properties.id, "my_node_data");
        console.log(e.features[0].properties.id)
        console.log("THIS IS THE POINT CLICKED")
        console.log(e.lngLat);
        ref.map.flyTo({
          center: e.lngLat,
          zoom:17
        })
        var AllTags = JSON.parse(e.features[0].properties.tags);
        var nameTag = AllTags.name;
        console.log(AllTags);
        console.log(nameTag);
        if(nameTag==undefined||nameTag=="undefined"){
          nameTag=e.features[0].properties.type;
          nameTag = "Punto Interesse"
        }
        ref.my_title=nameTag;
        //Getting All The Questions
        ref.getQuestion(e.features[0].properties.id,"NODE").then(items =>{
          console.log("THESE ARE THE POSSIBLE ANSWERS " + items[0].ANSWERS);
          const open_list = []
          const validate_list = []
          if(items.length!=0){
            for(var i in items){
              if(items[i].VALIDATING=="no"){
                open_list.push(items[i])
              }else{
                validate_list.push(items[i])
              }
            }
            ref.my_items = items;
            ref.my_open_quest = open_list;
            ref.my_validate_quest = validate_list
            ref.my_title = nameTag;
            console.log("USER LOCATION:" + geolocate._userLocationDotMarker._lngLat);
            console.log("CLICK LOCATION:" + e.lngLat);
            var distance;
            distance = ref.checkDistance(geolocate, e);
            //console.log("THIS IS DISTANCE: " + distance);
            ref.my_distance = distance;
            //console.log("yo"+ref.my_items)
            //console.log(ref.$refs.htmlPopup)
            ref.$nextTick(()=>{
              new maplibre.Popup().
              setLngLat(e.lngLat).
              //setHTML(my_html).
              setDOMContent(ref.$refs.htmlPopup.$el).
              //setHTML(ref.$refs.htmlPopup.$el.outerHTML).
              addTo(ref.map);
            })
          }
        })
      })
      ref.map.on('mouseenter', 'my_node_data', function () {
        ref.map.getCanvas().style.cursor = 'pointer';
      });

      //GEOJSON VERSION!!!!!!!!!!!!!!!!!!
      ref.map.addSource('my_centered',{
          type:'geojson',
          data: center_json_url,
      });
      ref.map.addLayer({
        'id': "my_center_data",
        'type': 'circle',
        'source': 'my_centered',
        "icon-allow-overlap": true,
        'maxzoom':20,
        'minzoom' : 14,
        'paint':{
          'circle-radius': 10,
          'circle-stroke-width': 3,
          'circle-color' : ["match",["get","id"],my_completed_ways,"transparent","#ECC478"],
          "circle-stroke-color" : ["match",["get","id"],my_completed_ways,"transparent","#6667AB"]
        }
      })
      ref.map.on('click', 'my_center_data',function(e){
        //////////////////////////////////////
        //PRIMA DI TUTTO DEVO VERIFICARE SE LA VIA  DA VISUALIZZARE O MENO! QUINDI VEDO SE  NELLE VIE COMPLETE O NO. 
        if(!ref.completed_w.includes(e.features[0].properties.id)){
          console.log(e.features[0].properties.id);
          console.log("THIS IS THE CLICKED ID: " + e.features[0].properties.id);
          console.log("OK NOW I SHOULD CHANGE COLOR!!!!");
          console.log("THIS IS THE POINT CLICKED")
          console.log(e.lngLat);
          ref.hideAllExceptOneWay(ref.map,e.features[0].properties.id, "my_way_data");
          ref.hideAllExceptOneCentered(ref.map,e.features[0].properties.id, "my_center_data");
          ref.map.flyTo({
            center: e.lngLat,
            zoom:17
          })
          //qua dovrei fare na chiamata e aspettare la risposta per poi caricare le doamnde corrispondenti al dato elemento.
          //SE NON C' GI IL POPUP DEVO FARLO APPARIRE QUINDI FACCIO CHIAMATA E FACCIO APPARIRE POPUP
          var popup_prova = document.getElementsByClassName("maplibregl-popup");
          if(popup_prova[0]==undefined){
            //CERCO LA VIA CON LO STESSO ID PER OTTENERE ANCHE I TAG
            const features = ref.map.queryRenderedFeatures({ layers: ['my_way_data'] });
            for(var feature of features){
              //console.log(feature.properties);
              if(feature.properties.id == e.features[0].properties.id){
                var tagName = JSON.parse(feature.properties.tags);
                var nameTag = tagName.name;
                var highwayType = tagName.highway;
                if(nameTag==undefined||nameTag=="undefined"){
                  if(highwayType==undefined||highwayType=="undefined"){
                    nameTag = feature.properties.type;
                  }else{
                    nameTag = "strada: "+highwayType;
                  }

                }
              }
            }
            //PRENDO LE DOMANDE
            ref.getQuestion(e.features[0].properties.id,"WAY").then(items =>{                
              const open_list = []
              const validate_list = []
              if(items.length!=0){
                for(var i in items){
                  if(items[i].VALIDATING=="no"){
                    open_list.push(items[i])
                  }else{
                    validate_list.push(items[i])
                  }
                }
                ref.my_items = items;
                ref.my_open_quest = open_list;
                ref.my_validate_quest = validate_list;
                ref.my_title=nameTag;
                console.log("USER LOCATION:" + geolocate._userLocationDotMarker._lngLat);
                console.log("CLICK LOCATION:" + e.lngLat);
                var distance;
                distance = ref.checkDistance(geolocate, e);
                //console.log("THIS IS DISTANCE: " + distance);
                ref.my_distance = distance;
                ref.$nextTick(()=>{
                  var my_popup = new maplibre.Popup().
                  setLngLat(e.lngLat).
                  setDOMContent(ref.$refs.htmlPopup.$el).
                  //setHTML(ref.$refs.htmlPopup.$el.outerHTML).
                  addTo(ref.map);
                  my_popup._container.style.backgroundColor = "rgba(255,255,255,0.8)";
                  my_popup._content.style.backgroundColor= "rgba(255,255,255,0.7)";
                })
              }
            })
          }
        }

      })
      ref.map.on('mouseenter', 'my_way_data', function () {
        ref.map.getCanvas().style.cursor = 'pointer';
      });
    }) 

    //RESET EVERYTHING WHEN CLICK OUTSIDE THE WAYS AND NODES
    ref.map.on('click', function(e) {
      // The event object (e) contains information like the
      // coordinates of the point on the map that was clicked.
      var popup_prova = document.getElementsByClassName("maplibregl-popup");
      if(popup_prova[0]==undefined || popup_prova[0]=="undefined"){
        console.log('A click event has occurred at ' + e.lngLat);
        //ref.updateAllCompleted()
        ref.showEverything(ref.map, my_completed_ways, my_completed_nodes);
      }else{
        if(popup_prova[0].style.display=="none"){
          console.log('A click event has occurred at ' + e.lngLat);
        }
        //ref.updateAllCompleted();
        ref.showEverything(ref.map, my_completed_ways, my_completed_nodes);
      }
    });
    
  },
  methods:{
    async getQuestion(id, node_or_way){
      try{
        const my_url = this.$api_url + "/posts/"+id+"&"+node_or_way
        const requestSpatialite = {
          method:"get",
          headers:{ "Content-Type":"application/json"},
        };
        const fetchdata = await fetch(my_url,requestSpatialite)
          .then(response => response.json())
          .then((new_response_data)=>{
            console.log(new_response_data); 
            return new_response_data;
          }).catch((err)=>console.log(err)) 
          return fetchdata
      }catch(e){
        alert("Error init");
      }
    },

    async getAllCompletedWaysAndNodes(_callback,type){
      try{
        var my_url = ""
        if(type == "way"){
          my_url = this.$api_url + "/posts/way/checkcompleted"
        }else{
          my_url = this.$api_url + "/posts/node/checkcompleted"
        }
        const requestSpatialite = {
          method:"get",
          headers:{ "Content-Type":"application/json"},
        };
        const fetchdata = await fetch(my_url,requestSpatialite)
          .then(response => response.json())
          .then((new_response_data)=>{
            console.log(new_response_data); 
            return new_response_data;
          }).catch((err)=>console.log(err)) 
          _callback();
          return fetchdata
      }catch(e){
        alert("Error init");
      }
    },

    hideAllExceptOneWay(map, exceptId, layerId){
      map.setPaintProperty(layerId, "line-color",
          ["match",
          ["get","id"], exceptId, "#6667AB ", "transparent"]);
      //qua dovrei fare na chiamata e aspettare la risposta per poi caricare le doamnde corrispondenti al dato elemento.
    },

    hideAllExceptOneCentered(map, exceptId, layerId){
      map.setPaintProperty(layerId, "circle-color",
          ["match",
          ["get","id"], exceptId, "#ECC478", "transparent"]);
      map.setPaintProperty(layerId, "circle-stroke-color",
          ["match",
          ["get","id"], exceptId, "#6667AB", "transparent"]);
    },

    hideAllExceptOneNode(map,exceptId,layerId){
      map.setPaintProperty(layerId, "circle-color",
          ["match",
          ["get","id"], exceptId, "#E4455E", "transparent"]);
      map.setPaintProperty(layerId, "circle-stroke-color",
          ["match",
          ["get","id"], exceptId, "#E4455E", "transparent"]);
    },

    hideAll(map){
      //NASCONDO I NODI
      map.setPaintProperty("my_node_data", "circle-color", "transparent");
      map.setPaintProperty("my_node_data", "circle-stroke-color", "transparent");
      //NASCONDO I CENTRALI DELLE VIE
      map.setPaintProperty("my_center_data", "circle-color", "transparent");
      map.setPaintProperty("my_center_data", "circle-stroke-color", "transparent");
      //NASCONDO TUTTE LE VIE
      map.setPaintProperty("my_way_data","line-color","transparent");
    },

    showEverything(map, my_completed_w, my_completed_n){
      map.setPaintProperty("my_way_data", "line-color",
          ["match",
          ["get","id"], my_completed_w, "transparent", "#6667AB"]);
      map.setPaintProperty("my_center_data", "circle-color", ["match",["get","id"],my_completed_w,"transparent","#ECC478"]);
      map.setPaintProperty("my_center_data", "circle-stroke-color", ["match",["get","id"],my_completed_w,"transparent","#6667AB"]);
      map.setPaintProperty("my_node_data", "circle-color", ["match",["get","id"],my_completed_n,"transparent","#E4455E"]);
      map.setPaintProperty("my_node_data", "circle-stroke-color", ["match",["get","id"],my_completed_n,"transparent","#E4455E"]);
      console.log("RESET ALL POAINTING PROPERTIES...");
      console.log(my_completed_n);
    },

    async updateAllCompleted(){
      //PRIMA DI TUTTO FACCIO CHIAMATA CHE MI DICE QUALI VIE MOSTRARE E QUALI NO
      var my_completed_ways_dt = await this.getAllCompletedWaysAndNodes(function(){
        console.log("finished getting all ways....")
      },"way")
      var my_completed_ways = []
      for(var i in my_completed_ways_dt){
        my_completed_ways.push(my_completed_ways_dt[i].id)
      }
      if(my_completed_ways.length == 0){
        my_completed_ways.push(0);
      }
      this.completed_w = my_completed_ways;
      var my_completed_nodes_dt = await this.getAllCompletedWaysAndNodes(function(){
      },"node")
      var my_completed_nodes = []
      for(var j in my_completed_nodes_dt){{
        my_completed_nodes.push(my_completed_nodes_dt[j].id)
        console.log(j);
      }}
      if(my_completed_nodes.length == 0){
        my_completed_nodes.push(0)
      }
      console.log("THESE ARE MY COMPLETED NODES::::::" + my_completed_nodes);
      //console.log("UPDATING::::::");
      this.completed_n = my_completed_nodes;
      console.log("THIS IS COMPLETED_N " + this.completed_n);
      this.showEverything(this.map,this.completed_w,this.completed_n);
      //showEverything()
    },

    createHTML(item){
      for(const i in item){
        this.createQuestion(item[i])
      }
      return '<h1> THIS IS MY QUESTION: <h1>'+'<p>'+item[0].QUESTION+'</p>'
    },

    async askGeoLocation(){
      //console.log("GETTING GEOLOCATION...")
      navigator.geolocation.getCurrentPosition(
        position => {
          console.log(position.coords.latitude);
          console.log(position.coords.longitude);
          this.location_permission=true;
        },
        error => {
          console.log("coudln't get coordinates"+error.message);
          this.location_permission=false;
          this.$refs.no_location_alert.second=true;
        },
      )   
    },

    checkDistance(geolocate,e){
      var distance
      //console.log(geolocate._userLocationDotMarker._lngLat)
      //console.log(e.lngLat);
      if(geolocate._userLocationDotMarker == undefined){
        distance = 50000
      }else{
        if(geolocate._userLocationDotMarker._lngLat == undefined){
          distance = 50000;
        }else{
          distance = (geolocate._userLocationDotMarker._lngLat).distanceTo(e.lngLat);
        }
      }
      //console.log("QUESTA  LA DISTANZA DATA DALLA FUNZIONE!")
      //console.log(distance);
      return distance
    }
  }
}

</script>

