import Vue from 'vue'
import VueMaterial from 'vue-material'
//import Vuetify from "vuetify/lib";
import 'vue-material/dist/vue-material.min.css'
import App from './App.vue'
import router from './router'
import Ionic from '@ionic/vue';
import '@ionic/core/css/ionic.bundle.css';
//import 'vue-material/dist/theme/default.css'//QUESTO VA A MODIFICARMI ANCHE IUL BOTTOM MENU!
import { MdButton, MdContent, MdTabs } from 'vue-material/dist/components'

// Import the Auth0 configuration
import { domain, clientId } from "../auth_config.json";

// Import the plugin here
import { Auth0Plugin } from "./utils/auth.js";

import './registerServiceWorker'

// Install the authentication plugin here
Vue.use(Auth0Plugin, {
  domain,
  clientId,
  onRedirectCallback: appState => {
    router.push(
      appState && appState.targetUrl
        ? appState.targetUrl
        : window.location.pathname
    );
  }
});

//Vue.prototype.$api_url = "http://localhost:5000"
//Vue.prototype.$api_url = "https://pwa-osm-bicycle-prova.herokuapp.com"
Vue.prototype.$api_url = "https://nodedataspatial.herokuapp.com"

Vue.use(VueMaterial)
Vue.use(Ionic);
Vue.config.productionTip = false
Vue.use(MdButton)
Vue.use(MdContent)
Vue.use(MdTabs)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
