<template>
  <div style="max-height: 300px; overflow-y: scroll; background-color:transparent;">
    <h1 v-if="this.items" class="my_title">{{title}}</h1>
    <ul>
      <li v-for="(value,index) in this.open" v-bind:key="'Q'+index" class="myQuestList">
        <QuestionForm :id="open[index].ID" :item="open[index].QUESTION" :type="open[index].TYPE" :score="open[index].SCORE" :possibilities="open[index].ANSWERS" ref="openquests" />
      </li>
      <li v-for="(value,index) in this.validate" v-bind:key="'V'+index">
        <ValidationForm  :id="validate[index].ID" :item="validate[index].QUESTION" :type="validate[index].TYPE" :score="validate[index].SCORE" ref="validatequests"/>
      </li>
    </ul>
    <ion-button v-on:click="onSubmit" class="my_button">Submit</ion-button>
    <LevelUp ref="level_up_alert"/>
    <NewMedal ref="new_medal_alert"/>
    <NoLocation ref="no_location_alert"/>
    <DistanceError ref="distance_alert"/>
  </div>
</template>
<script>
import QuestionForm from './questions/QuestionForm.vue'
import ValidationForm from './questions/ValidationForm.vue'
import LevelUp from './popups/LevelUp.vue'
import NewMedal from './popups/NewMedal.vue'
import NoLocation from './popups/NoLocation.vue'
import DistanceError from './popups/DistanceError.vue'
//import Vue from 'vue'

export default {
  name: 'Test',
  props: ['items','open','validate','title','location','distanza'],
  components:{
      QuestionForm,
      ValidationForm,
      LevelUp,
      NewMedal,
      NoLocation,
      DistanceError
  },
  data(){
      return{
        validate_list:[],
        open_list:[],
        email_login:"",
        password_login:""

      }
  },
  mounted: function(){
      const values = this.items
      console.log(this.items);
      for(var i in values){
        if(values[i].VALIDATING=="no"){
          this.open_list.push(values[i])
        }else{
          this.validate_list.push(values[i])
        }
      }
      //console.log("HEY THIS SHOULD BE THE TITLE" + this.title)
      //console.log("YOOOOOOOOOOOOOOOOOOOO")
      //console.log(this.$refs);
      //console.log(this.$vnode)
      //this.$refs.level_up_alert.second=true;
      //this.$refs.new_medal_alert.medalName = "spongebob";
      //this.$refs.new_medal_alert.second=true;
  },
  updated() {
    this.$nextTick(function () {
      //console.log("HEY THIS SHOULD BE THE TITLE AFTER UPDATE" + this.title)  
      console.log(this.items.length)
      console.log(this.open.length)
      console.log(this.validate.length)
      console.log(this.items)
      console.log(this.location);
      console.log(this.distanza);
      //DEVO CREARE ARRAY CON RISPOSTE
      //console.log("AFTER TICKKKKK");
      /*for(let i=0;i<this.open.length;i++){
        console.log("ENTERED FOR");
        console.log(this.open[i].ANSWERS);
      }*/
      this.$refs.openquests.forEach(i=>{
        i.updateAnswers();
      });
    // Code that will run only after the
    // entire view has been re-rendered
    })
  },
  methods:{
    async onSubmit(e){
      e.preventDefault();
      //console.log("THIS IS LOCATION: " + this.location);
      if(this.location == false){
        console.log("SORRY YOU HAVE TO GIVE LOCATION PERMISSION TO ANSWER QUESTIONS");
        this.$refs.no_location_alert.second=true;
        return;
      }else{
        console.log(this.location);
        if(this.distanza>200){
          console.log("EEEEH TROPPO DISTANTE");
          this.$refs.distance_alert.second=true;
          return;
        }else{
          console.log("AFTER IF");
          //console.log(this)
          var answer_to_send="";
          var answer ="";
          var question = "";
          var id ="";
          var type="";
          var score = null;
          var close_popup=false;
          var cancella=true; //serve per sapere se eliminare completamente la strada
          if(this.$refs.openquests!=undefined){
          this.$refs.openquests.forEach(async i=>{
            answer=i.answer;
            question=i.item;
            id=i.id;
            type=i.type
            score = i.score
            //VERIFICO CHE LA RISPOSTA NON SIA VUOTA SE NON  VUOTA INVIO RISPOSTA
            if(i.answer!=""){
              //invia risposta
              close_popup=true
              await this.sendAnswer(answer,id,question,type).then(async items=>{
                console.log(items)
                console.log("Worked?")
                //CHIAMO GAME ENGINE PER OTTENERE PUNTEGGIO
                await this.sendAnswerEngine("normal", this.$auth.user.nickname, score)
              })
            }else{
              //altrimenti setto booleana cancella a false
              cancella=false
            }
          })
          }
          //FARE LA STESSA COSA PER VALIDATE QUESTS
          if(this.$userData.level==null || this.$userData.level==undefined || this.$userData.level==""){
            //POPUP CHE DICE CHE DEVI RISPONDERE PRIMA A UNA MISSIONE NORMALE
            console.log("YOU CAN't ANSWER THIS!")
          }else{
            if(this.$refs.validatequests!=undefined){
              this.$refs.validatequests.forEach(async i=>{
                answer = i.answer;
                question=i.item;
                id=i.id;
                type=i.type;
                if(answer == true){
                  answer_to_send = "si"
                }else{
                  answer_to_send = "no"
                }
                if(i.answer!=""){
                  close_popup=true
                  await this.sendAnswer(answer_to_send,id,question,type).then(async items=>{
                    console.log(items)
                    console.log("workedval?")
                    //CHIAMO GAME ENGINE PER OTTENERE PUNTEGGIO
                    //SAR DA FARE CHIAMATA DIVERSA PER LE VALIDATION PERCH COSI NON VA
                    await this.sendAnswerEngine("validation", this.$auth.user.nickname, score)
                  })
                }else{
                  cancella=false
                }
              })
            }
          }
          if(cancella==true){
            //this.deleteGeometry(id,type)
            //cancella geometria della strada/nodo
            this.deleteGeometry(id,type).then(items=>{
              console.log("DELETED ALL" + items)
              ////////////////////
              var popup_prova = document.getElementsByClassName("maplibregl-popup");
              console.log(popup_prova)
              console.log(popup_prova[0].style)
              //FACCIO L'UPDATE DELLA MAPPA!
              this.$parent.updateAllCompleted();
              //popup_prova[0].style.display="none";
              ///////////////////
            })
          }
          if(close_popup){
            /////////////////////
            var popup_prova = document.getElementsByClassName("maplibregl-popup");
            console.log(popup_prova)
            console.log(popup_prova[0].style)
            //this.$parent.my_items=null;
            popup_prova[0].style.display="none";
            //MAGARI PRIMA MOSTRO POPUP CHE DICE "GRAZIE DI AVER RISPOSTO".
            ///////////////////
          }
        }
      }
      //this.$el.parentNode.removeChild(this.$el);
    },

    async deleteGeometry(id,type){
      console.log("entered_function "+id +type)
      var my_body = {
        "id":id,
        "type":type
      }
      console.log(type)
      try{
        const my_url = this.$api_url + "/posts/allAnswerCompleted"
        const requestSpatialite = {
          method:"post", 
          headers:{ "Content-Type":"application/json"},
          body: JSON.stringify(my_body)
        };
        const fetchdata = await fetch(my_url,requestSpatialite)
          .then(response => response.json())
          .then((new_response_data)=>{
            return new_response_data;
          }).catch((err)=>console.log(err))
          return fetchdata
      }catch(e){
        alert("Errorrrr")
      }
    },

    async sendAnswer(answer,id,question,type){
      var my_body = {
        "id": id,
        "answer": answer,
        "question": question,
        "type": type
      }
      console.log(my_body)
      try{
        const my_url = this.$api_url + "/missions/addAnswer"
        const requestSpatialite = {
          method:"post",
          headers:{ "Content-Type":"application/json"},
          body: JSON.stringify(my_body),
        };
        const fetchdata = await fetch(my_url,requestSpatialite)
          .then(response => response.text())
          .then((new_response_data)=>{
            //console.log("this is response" + new_response_data)
            //console.log(new_response_data)
            return new_response_data;
          }).catch((err)=>console.log(err)) 
          return fetchdata
      }catch(e){
        alert("Error init");
      }
    },

    async sendAnswerEngine(type,nickname,score){
      console.log("TYPE SENT"+type)
      console.log(nickname)
      console.log(score)
      var my_score = score.toString();
      //QUESTO LO AGGIUNGO PERCH MI D SOLO 1 PUNTO PER LE VALIDAZIONI NELLA GAME ENGINE
      if(type=="validation"){
        my_score="1";
        console.log("THIS IS MY NEW SCORE: " + my_score);
        score=1;
      }
      /////////////////////////////////////
      my_score = my_score + ".0";
      //console.log(my_score)
      var my_body = {
        "playerId" : nickname,
        "typeMission" : type,
        "points" : my_score
      }
      console.log(my_body)
      try{
        const my_url = this.$api_url + "/missions/givePoint"
        const requestSpatialite = {
          method:"post",
          headers:{"Content-Type":"application/json"},
          body: JSON.stringify(my_body),
        };
        const fetchdata = await fetch(my_url, requestSpatialite)
          .then(response => response.json())
          .then((new_response_data)=>{
            this.updateUserInfo(type,score);
            return new_response_data;
          }).catch((err)=>console.log(err))
          return fetchdata
      }catch(e){
        alert("Error......");
      }
    },

    updateUserInfo(type,score){
      var medalArray = [];
      console.log("ENTERED UPDATE");
      console.log(this.$userData)
      medalArray = this.$userData.missionComplete(type,score);
      if(this.$userData.just_leveled_up == true){
        console.log("LEVELUP");
        console.log(this.$refs)
        //CREO POPUP CHE DICE DI ESSER SALITO DI LIVELLO;
        this.$refs.level_up_alert.second=true;
        this.$userData.just_leveled_up = false;
      }
      if(medalArray.length!=0){
        for(let i=0;i<medalArray.length;i++){
          console.log("THIS IS A MEDAL: " + medalArray[i]);
          this.$refs.new_medal_alert.medalName = "Congratulations, You unlocked the " + medalArray[i] + " medal!";
          this.$refs.new_medal_alert.second=true;
        }
      }
      this.$parent.$parent.my_points=this.$userData.points;
      this.$parent.$parent.checkUserClass();
      //MANDA SEMPRE UN POPUP CHE DICE CHE HAI RISPOSTO....,
      //console.log("Actual points: "+this.$userData.points)
    }
  },

}
</script>

<style>
  .my_button{
    margin-left: auto;
    margin-right: 20px;
    float:right;
    --border-radius: 15px;
    --background: white;
    --color: $color1;
  }

  .my_title{
    display:flex;
    justify-content:center;
  }
</style>


