<template>
  <div class = "popup" id="popup" text-align="center" justify-content="center" display="flex">
        <ion-grid>
        <form> 
            <ion-col>
                <ion-item>
                    <ion-input :value="answer" @input="answer = $event.target.value" placeholder="Enter New Nickname" name="email_login"></ion-input>
                </ion-item>
            </ion-col>
            <ion-col>
                <ion-button v-on:click="onCancel" color = "primary">Cancel</ion-button>
                <ion-button v-on:click="onSubmit" color = "primary">Submit</ion-button>
            </ion-col>
        </form>
        </ion-grid>
  </div>
</template>
<script>
export default {
    name: "Popup",
    data(){
        return{
            answer:"",
        }
    },
    methods : {
        onCancel(){
            this.$parent.editing=false;
        },

        async onSubmit(){
            if(this.answer.length<5){
                console.log(this.answer)
                alert("Nickname must be at least 5 character long")
            }else{
                alert("Changing Nickname please wait...")
                var my_received_answer= await this.$parent.updateName(function(){
                    console.log("finished")
                    console.log(my_received_answer)
                },this.answer)
                this.$parent.editing=false;
            }
        },

        async changeNickName(_callback){
            var acc_token = await this.$auth.getTokenApi();
            var user_id = this.$auth.user.sub;
            console.log(acc_token);
            var token_to_use = "Bearer " + acc_token.access_token;
            var my_body = {
                "new_name": "provola",
                "user_id": user_id
            }
            
            var my_url = this.$api_url + "/posts/user/changeUsername"
            try{
                var my_request = {
                    method: "post",
                    headers:{ "Content-Type":"application/json", "Authorization": token_to_use},
                    body:JSON.stringify(my_body)
                }
                const fetchdata = await fetch(my_url,my_request)
                .then(response => response.json())
                .then((new_response_data)=>{
                    console.log(new_response_data); 
                    return new_response_data;
                }).catch((err)=>console.log(err))
                    _callback();
                    return fetchdata
            }catch(e){
                console.log(e)
            }
        }
    }
}
</script>

