<template>
  <div class= "ion-page">
    <ion-header>
      <ion-toolbar>
        <!--<ion-title>Medals</ion-title>-->
        <div style="display:inline-flex; overflow:auto; width:100%; background-color:#ECC478">
              <SideBarNav/>
              <h3 style="margin-top: 16px;">Medals</h3>
        </div>
      </ion-toolbar>
    </ion-header> 
    <br>
    <ion-content class = "ion-text-center" justify-content-center v-if="this.my_badges">
      <li class="medal_list" v-for="(value,index) in this.my_badges" v-bind:key="'M'+index">
        <MedalsInfo :medalName="my_badges[index]"/>
      </li>
      <ion-button class="back_button" @click="goToClassification()">Go Back</ion-button>
        <!--<MedalsInfo :medalsList="my_badges"/>-->
    </ion-content>
    <ion-content class = "ion-text-center" justify-content-center v-else>
        <NoMedalsClassInfo/>
        <ion-button class="back_button" @click="goToClassification()">Go Back</ion-button>
    </ion-content>
    
    <!--<BottomNav/>-->
  </div>
</template>

<script>
//import BottomNav from "../components/BottomNav";
import MedalsInfo from "../components/MedalsInfo";
import NoMedalsClassInfo from "../components/NoMedalsClassInfo";
import UserData from "../utils/UserData.js";
import Vue from 'vue'
import SideBarNav from "../components/sidebar/SideBarNav";

export default{
    name: "MedalUser",
    components: {MedalsInfo, NoMedalsClassInfo, SideBarNav},
    data(){
      return{ my_badges:null, nickname:null}
    },
    props: ['userName'],
    mounted: async function(){
        //Get Player Medals
        var nickname = this.$route.params.userName
        var userStats = new UserData();
        this.nickname=nickname;
        if(this.$userInfo==null||this.$userInfo==undefined){
            //console.log("NOT CREATED...");
            await(userStats.createUser(nickname,this.$api_url));
            this.updatePage(userStats);
            Vue.prototype.$userInfo = userStats;
        }else{
            if(this.$userInfo.userName==nickname){
                //console.log("Perfect");
                this.updatePage(this.$userInfo);
            }else{
                //console.log("Not the same");
                //console.log("nickname: " + nickname + "name: " + this.$userInfo.userName)
                await(userStats.createUser(nickname,this.$api_url));
                this.updatePage(userStats);
                Vue.prototype.$userInfo = userStats;
            }
        }
        //console.log(this.nickname);
  },

  methods:{
      updatePage(userData){
        //if(userData.badges.length)
          //console.log(userData.badges);
          if(userData.badges.length>0){
            this.my_badges=new Array();
            for(var i=0;i<userData.badges.length;i++){
              this.my_badges.push(userData.badges[i]);
            }
          }
          //console.log("..."+this.my_badges);
          //console.log("THESE ARE MY BADGES: " + this.my_badges)
      },
      goToClassification(){
            console.log("Clicked go to medal");
            //this.$router.push('/classification'); 
            this.$router.push({name:"userclassification", params:{userName: this.nickname}});
      },
  },
}
</script>
<style scope>
  .medal_list{
    list-style-type:none;
  }

  .back_button{
    --border-radius: 15px;
    --color: $color1;
    --background: white;
    width:200px;
    margin:0 auto;
    margin-bottom: 50px;
    margin-top:20px;
  }
</style>