<template>
  <div>
    <!--<h1>{{item.playerId}}</h1>-->
    <ion-button v-if="my_user" :id="item.playerId" class="my_user_button" @click="goToUserInfo()">{{item.playerId}}</ion-button>
    <ion-button v-else :id="item.playerId" class="medal_button" @click="goToUserInfo()">{{item.playerId}}</ion-button>
  </div>
</template>
<script>

export default{
    name: "PlayerField",
    props: ['item','index'],
    data(){
        return{
            x:"",
            my_user:false,
        }
    },
    mounted:function(){
      if(this.$userData.userName == this.item.playerId){
        this.my_user = true;
      }else{
        this.my_user = false;
      }
      //console.log(document.getElementsByClassName("medal_button"));
    },
    methods:{
      goToUserInfo(){
        this.$router.push({name:"userclassification", params:{userName: this.item.playerId}}); 
      }
    }
    /* eslint-disable */
}
</script>

<style scoped lang="scss">

.medal_button{
    --border-radius: 15px;
    --color: black;
    --background: white;
    width:80%;
}

.my_user_button{
    --border-radius: 15px;
    --color: white;
    --background: #6667AB;
