<template>
  <div class="ion-page">
    <ion-header>
      <ion-toolbar>
        <!--<ion-title>HomePage</ion-title>-->
        <div style="display:inline-flex; overflow:auto; width:100%; background-color:#ECC478">         
              <SideBarNav/>
              <h3 style="margin-top: 16px;">HomePage</h3>
              <div style=" float:right; margin-top:10px; margin-left: auto; margin-right: 25px;">
                <p style="margin:0"> Punteggio: {{my_points}}<!--Your Points: {{my_points}}--></p>
                <p style="margin:0">Classifica: {{my_ranking}}<!--Ranking: {{my_ranking}}--></p>
              </div>
        </div>
      </ion-toolbar>
    </ion-header> 
    <ion-content class = "ion-padding">
        <MyTiles/>
    </ion-content>
    <!--<BottomNav/>-->
  </div>
</template>

<script>
  // @ is an alias to /src
  import MyTiles from "../components/MyTiles";
  import SideBarNav from "../components/sidebar/SideBarNav"
  import UserData from "../utils/UserData.js";
  import Vue from 'vue'
  //import BottomNav from "../components/BottomNav";
  //console.log("called once");
  export default {
    name: 'TilesVector',
    components: { MyTiles, SideBarNav},
    data(){
      return{ my_points:null, my_ranking:"..."}
    },
    async mounted(){
      if(this.$userData==null||this.$userData==undefined){
            console.log("NOT CREATED...");
            Vue.prototype.$userData = new UserData();
            await(this.$userData.createUser(this.$auth.user.nickname, this.$api_url)).then(items=>{
              this.my_points = this.$userData.points;
              this.getRanking();
              console.log(items)
              console.log(" un nuovo giocatore?" + this.$userData.isNew)
              //Se  nuovo mostra tutorial, altrimenti continua //this.$router.push("tutorial"); 
            });
        }else{
          console.log("Already Created," + this.$userData);
          this.my_points=this.$userData.points;
          this.getRanking();
          console.log(" un nuovo giocatore?" + this.$userData.isNew)
          //Se  nuovo mostra tutorial, altrimenti continua //this.$router.push("tutorial"); 
        }
    },
    updated(){
      //console.log("AM I BEING UPDATED MAYBE YES")
      //this.my_points = this.$userData.points;
    },
    methods:{
      async checkUserClass(){
        console.log("CHECKING USER CLASSIFICATION AND THEN UPDATING::::::");
        await this.getRanking();
      },
      
      async getRanking(){
        await this.getPlayerClassification().then(async items=>{
          var my_content = items.content;
          //Ordino l'array dal player con punteggio pi alto al pi basso  
          my_content.sort(function(a,b){
              return parseFloat(b.state.PointConcept[1].score) - parseFloat(a.state.PointConcept[1].score);
          })
          //console.log("USERNAME:" + this.$userData.userName);
          for(var i=0; i<my_content.length;i++){
            if(my_content[i].playerId==this.$userData.userName){
              console.log(my_content[i].playerId);
              this.my_ranking=i+1;
              console.log(this.my_ranking);
            }
          }
        });
      },

      async getPlayerClassification(){
        //call api and get response;
        try{
            const my_url = this.$api_url + "/posts/user/getUserClassification"
            const requestToGamification = {
                method:"get",
                headers:{ "Content-Type":"application/json"},
            };
            const fetchdata = await fetch(my_url,requestToGamification)
            .then(response => response.json())
            .then((new_response_data)=>{
                return new_response_data;
            }).catch((err)=>console.log(err)) 
            return fetchdata
        }catch(e){
            alert("Error init");
        }
      }
    }
  }
</script>

