<template>
  <div>
    <div class="map-container" ref="myTutorialMapp"></div>
    <TutorialPopup :text="this.popup_text" ref="tutorial_popup"></TutorialPopup>
    <QuestTutorialPopup ref="questPopup" v-if="showQuests"/>
  </div>
</template>

<script>
import maplibre from 'maplibre-gl';
import TutorialPopup from '../popups/TutorialPopup'
import QuestTutorialPopup from '../popups/QuestTutorialPopup'

export default {
  name: "MapTutorial",
  components:{
      TutorialPopup,
      QuestTutorialPopup
  },
  data(){
    return{showQuests:null, map:null, my_title:"", popup_text:""}
  },
  mounted: async function(){
    const ref=this
    const mapStyle = "https://api.maptiler.com/maps/topo/style.json?key=S1aB9ElJLePXra57G3cm"
    console.log(mapStyle)
    const initialState = {
      //lng: 11.13695030023975,
      //lat: 46.069092939704404,
      lng: 11.11695030023975,
      lat: 46.069092939704404,
      zoom: 14
    };
    const startPoint = {
      lng: 11.11695030023975,
      lat: 46.069092939704404,
    }
    //Prova a cliccare sulla via in viola, in alternativa puoi cliccare anche il cerchio e otterrai lo stesso risultato!
    this.popup_text = "Cliccando una via colorata o un cerchio all'interno di essa potrai vederne le domande associate. Prova a cliccare la via o il cerchio associato."
    /*
    "type": "way",
    "id": 23815011,
    "tags": "{\"highway\":\"unclassified\",\"name\":\"Via Tommaso Gar\",\"oneway\":\"yes\",\"surface\":\"paved\"}",
    "color": "#7F6E43"

    //GEOMETRIA
    0: (2) [11.117128729820251, 46.06812076552367]
    1: (2) [11.117101907730103, 46.068161705829425]
    2: (2) [11.116903424263, 46.0686008817448]
    3: (2) [11.116817593574524, 46.0688130247913]
    4: (2) [11.116822957992554, 46.06886140817895]
    5: (2) [11.116865873336792, 46.068943287661426]
    6: (2) [11.116951704025269, 46.069088437354566]
    7: (2) [11.117193102836609, 46.069445727280794]
    8: (2) [11.117445230484009, 46.069609484390725]
    9: (2) [11.11766517162323, 46.06979184969137]
    */

   //PUNTO MEDIO {lng: 11.11695030023975, lat: 46.069092939704404}

    this.map = new maplibre.Map({
      container: this.$refs.myTutorialMapp,
      style: `${mapStyle}`,
      center: [initialState.lng, initialState.lat],
      zoom: initialState.zoom,
      maxZoom: 17,
      interactive: false
    });
    this.map.setMaxZoom(20);
    this.map.on('load',function(){
        ref.map.resize();
        ref.map.flyTo({
            center: startPoint,
            zoom:18
        })
        //CALL FIRST POPUP
        ref.$refs.tutorial_popup.text=ref.popup_text;
        ref.$refs.tutorial_popup.second=true;
        ref.map.addSource('route', {
            'type': 'geojson',
            'data': {
                'type': 'Feature',
                'properties': {},
                'geometry': {
                    'type': 'LineString',
                    'coordinates': [
                        [11.117128729820251, 46.06812076552367],
                        [11.117101907730103, 46.068161705829425],
                        [11.116903424263, 46.0686008817448],
                        [11.116817593574524, 46.0688130247913],
                        [11.116822957992554, 46.06886140817895],
                        [11.116865873336792, 46.068943287661426],
                        [11.116951704025269, 46.069088437354566],
                        [11.117193102836609, 46.069445727280794],
                        [11.117445230484009, 46.069609484390725],
                        [11.11766517162323, 46.06979184969137],
                    ]
                }
            }
        });
        ref.map.addLayer({
            'id': 'route',
            'type': 'line',
            'source': 'route',
            'layout':{
                'line-join': 'round',
                'line-cap': 'round',
            },
            'paint': {
                'line-color': "#6667AB",
                'line-width': 9,
            },
        });
        ref.map.on('click', 'route',function(e){
            ref.showQuests=true;
            ref.showQuestion(ref,e,"Strada");      
        });
        //AGGIUNTO IL PUNTO CENTRALE
        ref.map.addSource('my_centered',{
            'type':'geojson',
            'data': {
                'type': 'Feature',
                'properties': {},
                'geometry': {
                    'type': 'Point',
                    'coordinates': [
                        11.11695030023975, 46.069092939704404,
                    ]
                }
            }
        });
        ref.map.addLayer({
            'id': 'my_centered',
            'type': 'circle',
            'source': 'my_centered',
            'paint':{
                'circle-radius': 10,
                'circle-stroke-width': 3,
                'circle-color' : "#ECC478",
                "circle-stroke-color" : "#6667AB"
            }
        });
        ref.map.on('click', 'my_centered',function(e){
            console.log("click " + e);
            ref.showQuests=true;
            ref.showQuestion(ref,e,"Strada");
        });
    });
    
  },
  methods:{
    showQuestion(ref,e,title){
        ref.popup_text="Ora puoi selezionare una risposta (in questo caso gi selezionata). Prova ora a premere submit e inviare la risposta."
        ref.$refs.tutorial_popup.text=ref.popup_text;
        ref.$refs.tutorial_popup.second=true;
        console.log(title);
        ref.$nextTick(()=>{
            var my_popup = new maplibre.Popup().
            setLngLat(e.lngLat).
            setDOMContent(ref.$refs.questPopup.$el).
            //setHTML(ref.$refs.htmlPopup.$el.outerHTML).
            addTo(ref.map);
            //console.log(my_popup);
            //console.log(my_popup._container.style)
            my_popup._container.style.backgroundColor = "rgba(255,255,255,0.8)";
            my_popup._content.style.backgroundColor= "rgba(255,255,255,0.7)";
        })     
    },
  }
}

</script>

