<template>
  <div>
    <div>
      <img src="../assets/images/3289576_user_icon.png" style="width:100px; height:100px;">
      <!--<h2>{{ $auth.user.nickname }}</h2>-->
      <ion-item>
            <ion-input class="text-input" text-center :value=this.nickname :readonly=this.read_only></ion-input>
      </ion-item>

      <p>Level {{ this.level }}</p>
      <progress id="file" :value=value max="100"> 32% </progress>
      <!--<ion-progress-bar value="0.5"></ion-progress-bar>-->
    </div>
    
    <div>
        <p>Punteggio: {{this.goldPoints}}</p>
    </div>
    <br>
    <ion-grid>
        <ion-row justify-content-center>
            <ion-button class="medal_button" @click="go_to_medal">Medaglie</ion-button>
        </ion-row> 
        <ion-row>
            <p></p>
        </ion-row>
        <ion-row justify-content-center>
            <ion-button class="medal_button" @click="go_back">Go Back</ion-button>
        </ion-row>
    </ion-grid>
  </div>
</template>

<script>
import UserData from "../utils/UserData.js";
import Vue from 'vue'
export default{
    name: "UserClassInfo",
    data(){
        return{
            editing:null,
            read_only: null,
            nickname: null,
            my_icon:null,
            level:null,
            value:null,
            goldPoints:null
        }
    },
    props: ['userName'],
    methods:{
        async updateName(_callback,newName){
            var acc_token = await this.$auth.getTokenApi();
            var user_id = this.$auth.user.sub;
            console.log(acc_token);
            var token_to_use = "Bearer " + acc_token.access_token;
            var my_body = {
                "new_name": newName,
                "user_id": user_id
            }
            var my_url = this.$api_url + "/posts/user/changeUsername"
            try{
                var my_request = {
                    method: "post",
                    headers:{ "Content-Type":"application/json", "Authorization": token_to_use},
                    body:JSON.stringify(my_body)
                }
                const fetchdata = await fetch(my_url,my_request)
                .then(response => response.json())
                .then((new_response_data)=>{
                    console.log("AOOOO"+new_response_data);
                    this.nickname = newName 
                    this.$auth.user.nickname = newName
                    return new_response_data;
                }).catch((err)=>console.log(err))
                    _callback();
                    return fetchdata
            }catch(e){
                console.log(e)
            }
        },

        async createNamePopup(){
            console.log("clicked")
            this.editing=true;
        },

        go_to_medal(){
            //this.$router.push('/medalclassification'); 
            this.$router.push({name:"MedalUser", params:{userName: this.nickname}});
        },

        go_back(){
            this.$router.push('/classification');
        },

        updatePage(userData){
            console.log(userData);
            //AGGIORNO IL PROFILO
            this.level = userData.level;
            this.goldPoints = userData.points;
            var custom_levelUp = userData.level_up_points;
            var max_points = this.level*10;
            var my_value = custom_levelUp*100/max_points
            this.value = my_value;
            //Prova con userData
            //console.log(this.$userData);
        }
    },

    mounted:async function(){
        this.editing=false;
        this.read_only = true;
        var nickname = this.$route.params.userName
        this.nickname = nickname;
        console.log(nickname);
        var userStats = new UserData();
        if(this.$userInfo==null||this.$userInfo==undefined){
            //console.log("NOT CREATED...");
            await(userStats.createUser(nickname,this.$api_url));
            this.updatePage(userStats);
            Vue.prototype.$userInfo = userStats;
        }else{
            if(this.$userInfo.userName==nickname){
                //console.log("Perfect");
                this.updatePage(this.$userInfo);
            }else{
                //console.log("Not the same");
                //console.log("nickname: " + nickname + "name: " + this.$userInfo.userName)
                await(userStats.createUser(nickname,this.$api_url));
                this.updatePage(userStats);
                Vue.prototype.$userInfo = userStats;
            }
        }
        console.log(this.nickname);
    },
}
</script>

